import React, { Component } from 'react';
import { loadModel } from './services/data.js';
import WebcamCapture from './services/WebcamCapture';
import Loader from './services/Loader';
import './App.css';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = { loading: true };
  }

  componentWillMount() {
    loadModel().then(() => {
      this.setState({ loading: false });
    });

  }

  render() {
    const cam = this.state.loading ? (
      <Loader />
    ) : (
      <WebcamCapture />
    );

    return (
      <div className="App">
          {cam}
      </div>
    );
  }
}

export default App;



