import React from 'react';

class oldScreenshots extends React.Component {

  renderScreenshots(oldScreenshots) {
    return (
      <ul>
          {oldScreenshots.map(function(src, index) {
              if(src.substring(0,4) === 'data') {
                let max = 30; let min = -30;
                let rotationAngle = Math.floor(Math.random() * (max - min + 1)) + min;
                return <li key={index}><img src={src} style={{ marginRight: index*10, transform: `rotate(${rotationAngle}deg`  }} className="polaroid" height="50" width="50"/></li>;
              }
            })}
      </ul>
    );
  }

  render() {
    return (
      <div id="oldScreenshots">
        <div className="container">
          {this.renderScreenshots(this.props.screenshots)}
        </div>
      </div>
    );
  }
}

export default oldScreenshots;



