import React from 'react';
import Webcam from 'react-webcam';
import { predict } from './data.js';
import OldScreenshots from './OldScreenshots.js';

class WebcamCapture extends React.Component {

  constructor(props) {
    super(props);
    this.state = { imageSrc: '', screenshotSize: 224, oldScreenshots: [], classes: [], photoTaken: false };
  }

  setRef = (webcam) => {
    this.webcam = webcam;
  }

  capture = () => {
    this.setState(prevState => ({
      oldScreenshots: [...prevState.oldScreenshots, document.getElementById('screenshot').src],
      imageSrc: this.webcam.getScreenshot()
    }));

    const screenshot = document.getElementById('screenshot');
    let classes = [];
    if (screenshot.complete && screenshot.naturalHeight !== 0) {
      predict(screenshot).then(classes => this.setState({ classes, photoTaken: true }));
    } else {
      screenshot.onload = () => {
        predict(screenshot).then(classes => this.setState({ classes, photoTaken: true }));
      }
    }

  };

  renderClasses(categories) {
    if(categories.length != 10) {
      return <div id="categories"/>;
    }
    console.log("we gon try it", categories);
    return (
      <div id="categories">
        {categories.slice(0, 5).map(function(category, i){
          let label = category.className.split(',')[0];
          return <div className="category" key={i}><b className="probability">{(category.probability*100).toFixed(2)}%</b>{label}</div>;
        })}
      </div>
    );
  }

  render() {
    let max = 30; let min = -30;
    let rotationAngle = Math.floor(Math.random() * (max - min + 1)) + min;

    return (
      <div style={{ position: 'fixed', top: 0, bottom: 0, left: 0, right: 0 }}>
        <h1 id="welcomeText" style={{ display: this.state.photoTaken ? "none" : ""}}>Press the button below to take a picture!</h1>
        <img id="screenshot" className="polaroid" alt="Take a screenshot!" style={{ display: this.state.photoTaken ? "" : "none", transform: `translate(-50%, -50%) rotate(${rotationAngle}deg` }} src={this.state.imageSrc} height={this.state.screenshotSize} width={this.state.screenshotSize} />
        <Webcam
          height={window.innerHeight}
          width={window.innerWidth}
          className="webcam"
          audio={false}
          ref={this.setRef}
          screenshotFormat="image/jpeg"
        />
        <div id="captureButton" onClick={this.capture}><p className="hidden">Capture photo</p></div>
        <OldScreenshots screenshots={this.state.oldScreenshots} />
        {this.renderClasses(this.state.classes)}
      </div>
    );
  }
}

export default WebcamCapture;



